import { Component, OnInit } from '@angular/core';
import { AppBarModel } from '../../../ui/components/app-bar/shared/model/app-bar.model';
import {AbstractControl, FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {ButtonModel} from '../../../ui/components/button/shared/model/button.model';

@Component({
  selector: 'app-default',
  templateUrl: './default.component.html',
  styleUrls: ['./default.component.scss']
})
export class DefaultComponent implements OnInit {

    public appBarModel: AppBarModel;
    public buttonBlue: ButtonModel;

    constructor(
        private data: AppBarModel,
        private _formBuilder: FormBuilder,
        private buttonData: ButtonModel
    ) {
        this.appBarModel = data;
        this.appBarModel.type = 'left-back';
        this.appBarModel.title = 'Daftar';
        this.buttonBlue = buttonData;
        this.buttonBlue.title = 'Lanjutkan';
    }

    firstFormGroup: FormGroup;
    secondFormGroup: FormGroup;
    thirdFormGroup: FormGroup;
    isEditable = true;
    toggleError = false; // dummy error
    isResponsive = window.innerWidth < 400;

    keytab(event) {
        const nextInput = event.srcElement.nextElementSibling;
        const target = event.target || event.srcElement;
        const id = target.id;

        if (nextInput == null) {
            return;
        } else {
            nextInput.focus();
        }
    }

    confirm() {
        this.toggleError = !this.toggleError;
    }

    log(val) { console.log(val); }

    ngOnInit() {
        this.firstFormGroup = this._formBuilder.group({
            nama_perusahaan: ['', Validators.required]
        });
        this.secondFormGroup = this._formBuilder.group({
            name: ['', Validators.required],
            phone: ['', Validators.required],
            username: ['', Validators.required],
            password: ['', Validators.required],
            con_password: ['', Validators.required],
        });
        this.thirdFormGroup = this._formBuilder.group({
            number_confirmation_1: ['', Validators.maxLength(1)],
            number_confirmation_2: ['', Validators.maxLength(1)],
            number_confirmation_3: ['', Validators.maxLength(1)],
            number_confirmation_4: ['', Validators.maxLength(1)],
            number_confirmation_5: ['', Validators.maxLength(1)],
            number_confirmation_6: ['', Validators.maxLength(1)],
        });


    }
}
