import { Component, OnInit } from '@angular/core';
import { NavBottomModel } from '../shared/model/nav-bottom.model';
import {Router} from '@angular/router';

@Component({
  selector: 'app-nav-bottom',
  templateUrl: './nav-bottom.component.html',
  styleUrls: ['./nav-bottom.component.scss']
})
export class NavBottomComponent implements OnInit {
    public navBottomModel: NavBottomModel;

  constructor(
      private data: NavBottomModel,
      private router: Router
  ) {
      this.navBottomModel = data;
  }

    gotoLink(url) {
        this.router.navigateByUrl(url).then( (e) => {
            if (e) {
                console.log("Navigation is successful!");
            } else {
                console.log("Navigation has failed!");
            }
        });
    }

  ngOnInit() {
      console.log(this.data);
  }

}
