import { Injectable } from '@angular/core';

import { RatingReviewAnswersDTO } from '../dto/rating-review-answers.dto';
import { RatingReviewAnswersModel } from '../model/rating-review-answers.model';

@Injectable()
export class RatingReviewAnswersMapper {

    constructor() {}

    public mappingDTOToModel(
        dto: RatingReviewAnswersDTO
    ): RatingReviewAnswersModel {

        if (dto) {
            const model = new RatingReviewAnswersModel();

            model.name = dto.name;
            model.secureId = dto.secure_id;

            return model;
        } else {
            console.log('RatingReviewAnswersMapper:  Cannot mapping empty data');
            return null;
        }
    }

    public mappingDTOsToModels(
        dtos: RatingReviewAnswersDTO[]
    ): RatingReviewAnswersModel[] {

        if (dtos) {
            const model = dtos.reduce( (result, each) => {
                result.push( this.mappingDTOToModel(each) );
                return result;
            }, []);

            return model;
        } else {
            console.log('RatingReviewAnswersMapper:  Cannot mapping empty data');
            return [];
        }
    }
}
