import { Injectable } from '@angular/core';

import { RatingReviewDTO } from '../dto/rating-review.dto';
import { RatingReviewModel } from '../model/rating-review.model';
import { RatingReviewListMapper } from './rating-review-list.mapper';
import { RatingReviewQuestionsMapper } from './rating-review-questions.mapper';

@Injectable()
export class RatingReviewMapper {

    constructor(
        private ratingReviewListMapper: RatingReviewListMapper,
        private ratingReviewQuestionsMapper: RatingReviewQuestionsMapper
    ) {}

    public mappingDTOToModel(
        dto: RatingReviewDTO
    ): RatingReviewModel {

        if (dto) {
            const model = new RatingReviewModel();

            model.list = this.ratingReviewListMapper.mappingDTOsToModels(dto.list);
            model.questions = this.ratingReviewQuestionsMapper.mappingDTOsToModels(dto.questions);

            return model;
        } else {
            console.log('RatingReviewMapper: Cannot mapping empty data');
            return null;
        }
    }
}
