import { Injectable } from '@angular/core';

import { HttpBodyRespStatusDTO } from '../dto';
import { HttpBodyRespStatusModel } from '../model';

@Injectable({
    providedIn: 'root'
})
export class HttpBodyRespStatusMapper {

    constructor() { }

    public mappingDTOToModel(
        dto: HttpBodyRespStatusDTO
    ): HttpBodyRespStatusModel {
        if (dto) {
            const model = new HttpBodyRespStatusModel();

            model.errorDetail = dto.error_detail;
            model.messageClient = dto.message_client;
            model.messageServer = dto.message_server;
            model.statusCode = dto.status_code;

            return model;
        } else {
            console.error('HttpBodyRespStatusMapper : Cannot mapping empty data');

            return null;
        }
    }

}
