import { Injectable } from '@angular/core';

import { HttpBodyRespDTO } from '../dto';
import { HttpBodyRespModel } from '../model';

import { HttpBodyRespPaginationMapper } from './http-body-resp-pagination.mapper';
import { HttpBodyRespStatusMapper } from './http-body-resp-status.mapper';

@Injectable({
    providedIn: 'root'
})
export class HttpBodyRespMapper {

    constructor(
        private httpRespPaginationMapper: HttpBodyRespPaginationMapper,
        private httpRespStatusMapper: HttpBodyRespStatusMapper
    ) { }

    public cloneDTO(
        object: any
    ): HttpBodyRespDTO {
        if (object.pagination && object.result && object.status) {
            const dto = new HttpBodyRespDTO();

            dto.pagination = object.pagination;
            dto.result = object.result;
            dto.status = object.status;

            return dto;
        } else {
            return null;
        }
    }

    public mappingDTOToModel(
        dto: HttpBodyRespDTO
    ): HttpBodyRespModel {
        if (dto) {
            const model = new HttpBodyRespModel();

            model.pagination = this.httpRespPaginationMapper.mappingDTOToModel(dto.pagination);
            model.result = dto.result;
            model.status = this.httpRespStatusMapper.mappingDTOToModel(dto.status);

            return model;
        } else {
            console.error('HttpBodyRespMapper : Cannot mapping empty data');

            return null;
        }
    }

}
