import {Component, OnInit, OnDestroy} from '@angular/core';
import {MatDialog} from '@angular/material';
import {Subscription} from 'rxjs';

import {ConfirmDialogComponent} from 'src/app/ui/components/confirm-dialog/confirm-dialog';
import {ConfirmDialogModel} from 'src/app/ui/components/confirm-dialog/shared/model';
import {RatingReviewReqByQueryModel, RatingReviewModel} from '../../shared/model';
import {RatingReviewService} from '../../shared/service';
import {AppBarModel} from '../../../ui/components/app-bar/shared/model/app-bar.model';

@Component({
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit, OnDestroy {

    private subscribers: Subscription[];
    private paramsModel: RatingReviewReqByQueryModel;
    private dialogModel: ConfirmDialogModel;

    public ratingReviewModel: RatingReviewModel;
    public appBarModel: AppBarModel;

    constructor(
        private dialog: MatDialog,
        private ratingReviewService: RatingReviewService,
        private data: AppBarModel
    ) {
      this.appBarModel = data;
        this.appBarModel.type = 'center';
        this.appBarModel.title = 'Pindai Kode QR';
    }

    ngOnInit() {
        this.subscribers = [];
        this.ratingReviewModel = new RatingReviewModel();
        this.paramsModel = new RatingReviewReqByQueryModel();
        this.dialogModel = new ConfirmDialogModel();

        this.getRatingReviewList();
    }

    ngOnDestroy() {
        this.subscribers.forEach(each => each.unsubscribe());
    }

    private getRatingReviewList() {
        const subs = this.ratingReviewService.getRatingReviewList(this.paramsModel)
            .subscribe(resp => {
                this.ratingReviewModel = resp;
                console.log(this.ratingReviewModel);
            });

        this.subscribers.push(subs);
    }

    public clickDialog() {
        const dialogRef = this.dialog.open(ConfirmDialogComponent, {
            data: this.dialogModel
        });
        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            console.log(result);
        });
    }

}
