import {Component, OnInit} from '@angular/core';
import {AppBarModel} from '../../../ui/components/app-bar/shared/model/app-bar.model';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.scss']
})

export class RatingComponent implements OnInit {
    rating: number;
    formGroup: FormGroup;

    constructor(private appbarModel: AppBarModel, private _formBuilder: FormBuilder) {
        appbarModel.type = 'left-back';
        appbarModel.title = 'Kembali';
    }

    ngOnInit() {
        this.formGroup = this._formBuilder.group({
            rating: ['', Validators.required]
        });
    }

    changeRating($event) {
        this.rating = $event.rating;
    }

    chipClicked(event) {
        const element = event.target || event.srcElement || event.currentTarget;
        if (element.classList) {
            element.classList.toggle('mat-chip-selected');
        } else {
            const classes = element.className.split(' ');
            const i = classes.indexOf('mat-chip-selected');

            if (i >= 0) {
                classes.splice(i, 1);
            } else {
                classes.push('mat-chip-selected');
                element.className = classes.join(' ');
            }
        }
    }

}
