import { Injectable } from '@angular/core';

import { RatingReviewListDTO } from '../dto/rating-review-list.dto';
import { RatingReviewListModel } from '../model/rating-review-list.model';

@Injectable()
export class RatingReviewListMapper {

    constructor() {}

    public mappingDTOToModel(
        dto: RatingReviewListDTO
    ): RatingReviewListModel {

        if (dto) {
            const model = new RatingReviewListModel();

            model.id = dto.id;
            model.shiftId = dto.shift_id;
            model.shift = dto.shift;
            model.timestamp = dto.timestamp;
            model.date = dto.date;
            model.customerId = dto.customer_id;
            model.customerName = dto.customer_name;
            model.canteenName = dto.canteen_name;

            return model;
        } else {
            console.log('RatingReviewListMapper: Cannot mapping empty data');
            return null;
        }
    }

    public mappingDTOsToModels(
        dtos: RatingReviewListDTO[]
    ): RatingReviewListModel[] {

        if (dtos) {
            const models = dtos.reduce( (result, each) => {
                result.push( this.mappingDTOToModel(each) );
                return result;
            }, []);

            return models;
        } else {
            console.log('RatingReviewListMapper: Cannot mapping empty data');
            return [];
        }
    }
}
