import { Component, OnInit } from '@angular/core';
import {AppBarModel} from '../../../ui/components/app-bar/shared/model/app-bar.model';

@Component({
  selector: 'app-search-default',
  templateUrl: './search-default.component.html',
  styleUrls: ['./search-default.component.scss']
})
export class SearchDefaultComponent implements OnInit {

    public appBarModel: AppBarModel;
    emptyInput = true; // yang membedakan search default dan result hanya ini

    constructor(
        private data: AppBarModel,
    ) {
        this.appBarModel = data;
        this.appBarModel.type = 'left-search';
    }

  ngOnInit() {
  }

}
