import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

import {ListComponent} from './pages/list';
import {QrScanComponent} from './pages/qr-scan/qr-scan.component';
import {ListRatingComponent} from './pages/list-rating/list-rating.component';
import {RatingComponent} from './pages/rating/rating.component';

const prefix = 'rating-review';

const routes: Routes = [
   {
        path: prefix + '/list',
        component: ListComponent
    },
    {
        path: prefix + '/qr-scan',
        component: QrScanComponent
    },
    {
        path: prefix + '/list-rating',
        component: ListRatingComponent
    },
    {
        path: prefix + '/rating',
        component: RatingComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class RatingReviewRoutingModule {
}
