import { Component, OnInit } from '@angular/core';
import {AppBarModel} from '../../../ui/components/app-bar/shared/model/app-bar.model';

@Component({
  selector: 'app-not-register',
  templateUrl: './not-register.component.html',
  styleUrls: ['./not-register.component.scss']
})
export class NotRegisterComponent implements OnInit {

    public appBarModel: AppBarModel;

    constructor(private data: AppBarModel) {
        this.appBarModel = data;
        this.appBarModel.type = 'left-close';
        this.appBarModel.title = '';
    }

  ngOnInit() {
  }

}
