import { Component, OnInit } from '@angular/core';
import {FormControl, Validators} from '@angular/forms';
import {AppBarModel} from '../../../ui/components/app-bar/shared/model/app-bar.model';

@Component({
  selector: 'app-new',
  templateUrl: './new.component.html',
  styleUrls: ['./new.component.scss']
})

export class NewComponent implements OnInit {

    public appBarModel: AppBarModel;

    username = new FormControl('', [Validators.required]);
    password = new FormControl('', [Validators.required]);

    getUsernameErrorMessage() {
        return this.username.hasError('required') ? 'Wajib Diisi' : '';
    }

    getPasswordErrorMessage() {
        return this.password.hasError('required') ? 'Wajib Diisi' : '';
    }

    constructor(
        private data: AppBarModel,
    ) {
        this.appBarModel = data;
        this.appBarModel.type = 'left-close';
        this.appBarModel.title = 'Login';
    }

  ngOnInit() {
  }

}
