import { Injectable } from '@angular/core';

import { HttpBodyRespPaginationDTO } from '../dto';
import { HttpBodyRespPaginationModel } from '../model';

@Injectable({
    providedIn: 'root'
})
export class HttpBodyRespPaginationMapper {

    constructor() { }

    public mappingDTOToModel(
        dto: HttpBodyRespPaginationDTO
    ): HttpBodyRespPaginationModel {
        if (dto) {
            const model = new HttpBodyRespPaginationModel();

            model.totalData = dto.total_data;
            model.totalPage = dto.total_page;

            return model;
        } else {
            console.error('HttpBodyRespPaginationMapper : Cannot mapping empty data');

            return null;
        }
    }

}
