import { Injectable } from '@angular/core';

import { RatingReviewQuestionsDTO } from '../dto/rating-review-questions.dto';
import { RatingReviewQuestionsModel } from '../model/rating-review-questions.model';
import { RatingReviewAnswersMapper } from './rating-review-answers.mapper';

@Injectable()
export class RatingReviewQuestionsMapper {

    constructor(
        private ratingReviewAnswersMapper: RatingReviewAnswersMapper
    ) {}

    public mappingDTOToModel(
        dto: RatingReviewQuestionsDTO
    ): RatingReviewQuestionsModel {

        if (dto) {
            const model = new RatingReviewQuestionsModel();

            model.id = dto.id;
            model.questionName = dto.question_name;
            model.rating = dto.rating;
            model.answerTypeId = dto.answer_type_id;
            model.answerType = dto.answer_type;
            model.answers = this.ratingReviewAnswersMapper.mappingDTOsToModels(dto.answers);

            return model;
        } else {
            console.log('RatingReviewQuestionsMapper: Cannot mapping empty data');
            return null;
        }
    }

    public mappingDTOsToModels(
        dtos: RatingReviewQuestionsDTO[]
    ): RatingReviewQuestionsModel[] {

        if (dtos) {
            const models = dtos.reduce( (result, each) => {
                result.push( this.mappingDTOToModel(each) );
                return result;
            }, []);

            return models;
        } else {
            console.log('RatingReviewQuestionsMapper: Cannot mapping empty data');
            return [];
        }
    }
}
