import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import {
  RatingReviewReqByQueryModel,
  RatingReviewModel
} from '../model';
import { API_LIST_RATING_REVIEW } from '../const';
import { generateHttpParams } from '../../../core/util';
import { HttpBodyRespDTO } from '../../../core/http-body-resp/dto';
import { HttpBodyRespMapper } from '../../../core/http-body-resp/mapper';
import { HttpBodyRespModel } from '../../../core/http-body-resp/model';
import { RatingReviewMapper } from '../mapper';
import { RatingReviewDTO } from '../dto';

@Injectable()
export class RatingReviewService {

  constructor(
    private http: HttpClient,
    private httpBodyRespMapper: HttpBodyRespMapper,
    private ratingReviewMapper: RatingReviewMapper
  ) { }

  getRatingReviewList(
    paramsModel: RatingReviewReqByQueryModel
  ): Observable<RatingReviewModel> {
    const paramsDTO = paramsModel;
    const params = generateHttpParams(paramsDTO);
    const url = `${API_LIST_RATING_REVIEW}`;

    return this.http.get(url, { params: params })
        .pipe(
            map((resp: HttpBodyRespDTO): HttpBodyRespModel =>
                this.httpBodyRespMapper.mappingDTOToModel(resp)
            ),
            map((model: HttpBodyRespModel): RatingReviewModel => {
              return (model.status.statusCode === 20)
                ? this.ratingReviewMapper.mappingDTOToModel(model.result as RatingReviewDTO)
                : null;
            })
        );
  }
}
